import { DropdownItem } from "./MultilevelMenu";
import { FaBell } from "react-icons/fa";
import { useIdentity } from "../hooks/identity";
import { useState, useEffect } from "react";

export function PushSubToggle() {
    var [id, axios] = useIdentity();
    const [subs, setSubs] = useState({});
    var activeUser = id.users[id.active];

    useEffect(() => {
        setSubs(JSON.parse(localStorage.getItem("notifications") ||"{}" ));
    }, []);

    function onClick() {
        //
        if(!"serviceWorker" in navigator) {return;}

        

        setSubs((prev)=>{
            var lol = {...prev};
            lol[id.users[id.active].username] = !prev[id.users[id.active].username];
            localStorage.setItem("notifications", JSON.stringify(lol));
            return lol;
        });
    }


    var on = !!activeUser && !!subs[activeUser.username];
    var statText = on?"ON":"OFF";
    if(typeof Notification !== "undefined" && Notification.permission === "denied") {
        on = false;
        statText = "DENIED";
    }
    if(typeof navigator === "undefined" || !"serviceWorker" in navigator) {
        on = false;
        statText = "UNSUPPORTED";
    }

    return (
    <DropdownItem 
        onClick={onClick}
        leftIcon={<FaBell />} 
        text={<>Notifications: <span style={{ color: on?"lime":"red" }}>{statText}</span></>}
    ></DropdownItem>);
}

